@import "../../assets/scss/variables";


.ListItem {
  display: flex;
  align-items: center;
  width: 100%;
  height: auto;
  transition: background-color 0.2s ease;
  border-radius: 5px;

  &.active {
    background-color: rgba(150, 150, 150, 0.5);
    position: relative;
    .ListItem__title{
      text-decoration: line-through;
    }
  }

  &__title {
    margin-left: 30px;
  }

  &__remove {
    margin-left: auto !important;
  }
}