$primaryColor: #3D82EB;
$backgroundColor: #F8F8F8;
$backgroundColorDark: #141419;


$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 720;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 480.01;

.container {
  max-width: $maxWidthContainer + px;
  margin: 0 auto;
  width: 100%;
  @media (max-width: $md1 + px) {
    padding: 0 16px;
  }
  @media (max-width: $md2+px) {
    padding: 0 16px;
  }
  @media (max-width: $md3+px) {
    max-width: 100%;
  }
}